import React, {useEffect} from 'react';
import {Formik, Form, Field, ErrorMessage} from 'formik';
import f from "./Login.module.css";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
const Basic = ({login,isAuth}) => (


    <div>

        <Formik
            initialValues={{email: '', password: ''}}
            validate={values => {
                const errors = {};
                if (!values.email) {
                    errors.email = 'Required';
                } else if (
                    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                ) {
                    errors.email = 'Invalid email address';
                }
                return errors;
            }}

            onSubmit={(values, {setSubmitting}) => {

                login(values.email,values.password,values.rememberMe)
                setSubmitting(false)
                if({isAuth}) return window.location.replace('/profile')

            }
            }
        >
            {({isSubmitting}) => (
                <Form className={f.wrapper}>
                    <Field className={f.field} type="email" name="email"/>
                    <ErrorMessage name="email" component="div"/>
                    <Field className={f.field} type="password" name="password"/>
                    <ErrorMessage name="password" component="div"/>
                    <div>
                        <Field component={'input'} name={'rememberMe'} type={"checkbox"}/> remember me
                    </div>
                    <button type="submit" disabled={isSubmitting}>
                        Login
                    </button>
                </Form>
            )}
        </Formik>
    </div>
);

const mapStateToProps = (state) =>({
    isAuth:state.auth.isAuth
})

export default connect(mapStateToProps, {login})(Basic);