import f from './Dialogs.module.css'
import React from "react";
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import { Navigate } from "react-router-dom"

const Dialogs = (props) => {

    let state = props.dialogsPage;

    let dialogsElements = state.dialogs.map((dialog) =>
        <DialogItem name={dialog.name} key={dialog.id} id={dialog.id} avatar={dialog.avatar}/>)

    let messagesElements = state.messages.map((message) =>
        <Message message={message.message} key={message.id}/>)

    let newMessageBody = state.newMessageBody

    let onSendMessageClick = () => {
        props.sendMessage()
    }

    let onNewMessageChange = (e) => {
        let body = e.target.value;
        props.updateNewMessageBody(body);
    }

    if (!props.isAuth) return <Navigate to="/login"/>

    return (
        <div>
            <div className={f.dialogs}>
                <div className={f.dialogsItems}>
                    {dialogsElements}
                </div>
                <div className={f.messages}>
                    <div>{messagesElements}</div>
                    <div className={f.addMessages}>
                        <textarea
                            onChange={onNewMessageChange}
                            value={newMessageBody}
                            rows={3}
                            placeholder='Enter your message'/>
                        <button onClick={onSendMessageClick}></button>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Dialogs