import f from './ProfileInfo.module.css'
import React from "react";
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatus from "./ProfileStatus"
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus}) => {
    if (!profile) {
        return <Preloader/>
    }

    return (
        <div>
            <div>
                <img alt="" className={f.backgroundImg}
                     src={'https://www.vexels.com/blog/wp-content/uploads/2014/12/Elder-Gods-by-Yuri-Shwedoff.jpg'}/>
                <div className={f.avatar}>
                    <img alt="" className={f.avatarImg}
                         src={profile.photos.large}/>
                </div>
            </div>
            <div className={f.descProf}>
                <p>{profile.fullName}</p>
                <div className={f.stick}></div>
                <p>{profile.lookingForAJobDescription}</p>
                <div className={f.stick}></div>
                <p>{profile.aboutMe}</p>
            </div>
            <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>
        </div>
    )
}
export default ProfileInfo;