const UPDATE_NEW_MESSAGES_BODY = 'UPDATE_NEW_MESSAGES_BODY';
const SEND_MASSAGE = 'SEND_MASSAGE';

let initialState = {
    messages: [
        {id: 1, message: 'kley'},
        {id: 2, message: 'ky'},
        {id: 3, message: 'hi'},
        {id: 4, message: 'bagurrrgl'},
        {id: 5, message: 'kurlbIk'}
    ],
    dialogs: [
        {id: 1, name: 'Den', avatar: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQRbiMjUoOxJCAMB9poSO2wLg34m7OxmyaT-A&usqp=CAU'},
        {id: 2, name: 'Lesha', avatar: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSGs85L6VnAjWyJwi8u5v6K2N765HnHV-pVxQ&usqp=CAU'},
        {id: 3, name: 'Schweppes', avatar: 'https://upload.wikimedia.org/wikipedia/commons/0/09/Schweppes_Logo_2016.png'},
        {id: 4, name: 'Vanya', avatar: 'https://static.vecteezy.com/system/resources/previews/006/487/917/original/man-avatar-icon-free-vector.jpg'},
        {id: 5, name: 'iceFrog', avatar: 'https://cdn.dribbble.com/users/328610/screenshots/1664830/media/d225e2665a9ea6fe4672a6acc46db03b.png?compress=1&resize=400x300'}
    ],
    newMessageBody: ""
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_NEW_MESSAGES_BODY:
            return {
                ...state,
                newMessageBody: action.body
            }
        case SEND_MASSAGE:
            let body = state.newMessageBody;
            return {
                ...state,
                newMessageBody: '',
                messages: [...state.messages, {id: 6, message: body}]
            }
        default:
            return state
    }
}

export const sendMessageCreator = () => ({type: SEND_MASSAGE})
export const updateNewMessageBodyCreator = (body) => ({
    type: UPDATE_NEW_MESSAGES_BODY, body: body})

export default dialogsReducer